/*.................................................................here we give the dimension to the app..........................................*/
.App{
  width: 99vw;
  height: 99vh;



}

/*...........................................................Navbar position button.............................................*/

.btn-position{
  margin-left: 10%;
  width: 80%;
  margin-top: 60px;
  margin-left: none;
  height: 10%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}



.grp-name{
  margin-top: 20px;
  font-style: Cursive;
  font-weight: 500;
  font-size: 20px;
}

/*.......................................................css of the all avilable button..................................................*/

.btn{
  height: 60%;
  margin-top: 1.1%;
  width: 8%;
  border: 3px solid rgb(22, 22, 22);
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.438);
}


/*...................................................css of the hover button container.....................................................*/

#button-container{
  width: 23%;
  height: 15%;
  margin-left: 39.5%;
  margin-top: 12%;
}


/*................................................special feature added in the hover button..............................................*/

.hover-btn{
  height: 100%;
  width: 100%;
  font-size: 2.5rem;

}

/*...............................................................copyright content........................................................*/


.copyright{
  font-size: 1rem;
  font-weight: 350;
  margin-left: 40%;
  margin-top: 10%;
  width: 24%;
  background-color: rgba(104, 23, 227, 0.1);
  padding: 3px 3px 3px 7px;
  border-radius: 7px;
}


/*................................................................css for the top position hover........................................*/

.top{
  height: 30%;
  width: 50%;
  margin-top: -50%;
  margin-left: 20%;
  padding: 6px 10px 15px 10px;
  border: 1px solid black;
  border-radius: 10px;
  font-size: 1rem;
  background-color: rgba(104, 23, 227, 0.1);
  
}


/*................................................................css for the right position hover........................................*/


.right{
    height: 30%;
    width: 50%;
    margin-top:-22%;
    margin-left: 105%;
    padding: 6px 10px 15px 10px;
    border: 1px solid black;
    border-radius: 10px;
    font-size: 1rem;
    background-color: rgba(104, 23, 227, 0.1);
}


/*................................................................css for the down position hover........................................*/

.down{
  height: 30%;
  width: 50%;
  margin-top: 5%;
  margin-left: 20%;
  padding: 6px 10px 15px 10px;
  border: 1px solid black;
  border-radius: 10px;
  font-size: 1rem;
  background-color: rgba(104, 23, 227, 0.1);
}


/*................................................................csss for the left position hover........................................*/

.left{
  height: 30%;
  width: 50%;
  margin-top:-22%;
  margin-left: -60%;
  padding: 6px 10px 15px 10px;
  border: 1px solid black;
  border-radius: 10px;
  font-size: 1rem;
  background-color: rgba(104, 23, 227, 0.1);
}
.active{
  background-color:rgba(25, 43, 46, 0.3) ;
  color: wheat;
}
